<template lang="pug">
#App
    .introContainer
        h1.sectionTitle TechLifeProbe
        .introText Using Mobile and Wearable Technologies to Improve Adolescents' Smartphone Addiction Through Sharing Personal Data with Their Parents
    p UbiComp Lab 
    p National Tsing Hua University	
    .newsContainer
        h1.sectionTitle Recent news
        router-link.newsTitleBlock(v-for="(item,index) in newsList" :to='"post/"+index' :key='index')
            .newsTitle {{item.updated_at}}
                span {{item.title}}
</template>

<script>
export default {
    props: ['newsList'],
    data () {
        return {
            intro_img: ''
        }
    }
}
</script>


<style lang="sass" scoped>
.introContainer
    width: 100%
    
.introText
    font-size: 19px
.newsTitleBlock
    cursor: pointer
.newsTitle
    font-size: 22px
    margin: 15px
    overflow: hidden
    white-space: nowrap
    text-overflow: ellipsis
    color: #bbb
    span
        margin-left: 15px
        color: #333

</style>



