<template lang="pug">
#menuContainer
    router-link.menuItem(v-for='(item,i) in menuList' :to="item.link" :key="i") {{item.name}}
        #underline
    #drawer(v-if="drawerStatus")
        .drawerItem(v-for='item in menuList' @click='opentheDrawer()') 
            router-link(:to="item.link") {{item.name}}
                #underline
    .menuIcon(@click='opentheDrawer()' :class="menuclassList")
        .menuIconBar1
        .menuIconBar2
        .menuIconBar3
</template>

<script>
export default {
    
    data () {
        return {
            menuList: [
                {name: "Home",link: "/"},
                {name: "Members",link: "/members"},
                {name: "Projects",link: "/projects"},
                {name: "Publications",link: "/publications"},
                {name: "News",link: "/news"},
                {name: "Videos",link: "/videos"},
                {name: "FAQs",link: "/FQAs"}
            ],
            drawerStatus: false,
            menuclassList: {
                menuIconContainer: true,
                change: false
            }
        }
    },
    watch:{ 
    },
    mounted() {
    },
    methods: {
        opentheDrawer: function(){
            this.drawerStatus = !this.drawerStatus;
            this.menuclassList.change = !this.menuclassList.change;
        }
    }
}

</script>

<style lang="sass">
@import "@/style/value.sass"

#menuContainer
    position: fixed
    right: 0px
    left: 0px
    display: flex
    flex-wrap: nowrap
    justify-content: flex-end
    padding: 10px
    background-color: #ffffffaa
    
.menuItem
    cursor: pointer
    margin-right: 20px
    margin-top: 10px
    font-weight: 300

.menuIcon
    cursor: pointer

.menuIconBar1, .menuIconBar2, .menuIconBar3
    width: 35px
    height: 3px
    background-color: $c-text
    transition: 0.3s
    margin: 8px
    position: relative
    border-radius: 25px
.change .menuIconBar1
    opacity: 0
    
.change .menuIconBar2 
    -webkit-transform: rotate(-20deg) translate(-6px, 6px)
    transform: rotate(-20deg)

.change .menuIconBar3 
    -webkit-transform: rotate(225deg) 
    transform: rotate(225deg) translate(8px, 8px)
    transition-delay: 0.2s
    
#drawer
    background-color: #fff
    opacity: 0.9
    position: fixed
    text-align: center
    width: 100%
    right: 0px
    top: 0px
    bottom: 0px
    padding-top: 20%
    
.drawerItem
    cursor: pointer
    padding: 15px
    font-family: 'Krona One', sans-serif

.drawerItem:hover #underline
    width: 100px
.menuItem:hover #underline
    width: 100%

@media all and (min-width: 960px)
    .menuItem
        opacity: 1
    .menuIcon,#drawer
        opacity: 0
        transition: 0.3s
        visibility: hidden
    
@media all and (max-width: 960px)
    .menuItem
        opacity: 0
        transition: 0.3s
        visibility: hidden
    .menuIcon,#drawer
        opacity: 0.9


</style>



